import React, { useEffect, useState } from 'react';
import axios from 'axios';
import throttle from 'lodash.throttle';

const gistId = 'f88399ad853e6254fd1118468149e898'; // Replace this with your Gist ID
const githubToken = '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'; // Replace this with your GitHub Personal Access Token

const VisitorCount = () => {
  const [visits, setVisits] = useState(0);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchVisitorData = throttle(async () => {
      try {
        // Fetch IP address
        console.log('Fetching visitor IP address...');
        const ipResponse = await axios.get('https://ipinfo.io/json?token=425691027acbe5');
        const ip = ipResponse.data.ip;
        console.log(`Visitor IP: ${ip}`);

        // Get user agent
        const userAgent = navigator.userAgent;
        console.log(`User Agent: ${userAgent}`);

        // Create a unique key for the visitor based on IP and user agent
        const visitorKey = `${ip}_${userAgent}`;
        console.log(`Visitor Key: ${visitorKey}`);

        // Fetch existing visitor data from the GitHub Gist
        console.log('Fetching existing visitors from Gist...');
        const gistResponse = await axios.get(`https://api.github.com/gists/${gistId}`, {
          headers: {
            Authorization: `token ${githubToken}`
          }
        });

        let storedVisitors = [];
        if (gistResponse.data.files['visitor-count.json'] && gistResponse.data.files['visitor-count.json'].content) {
          storedVisitors = JSON.parse(gistResponse.data.files['visitor-count.json'].content).visitors || [];
        }
        console.log(`Stored Visitors: ${JSON.stringify(storedVisitors)}`);

        // Check if the visitor already exists
        if (!storedVisitors.includes(visitorKey)) {
          // Add new visitor
          storedVisitors.push(visitorKey);
          console.log(`Updated Visitors: ${JSON.stringify(storedVisitors)}`);

          // Update visitor data in the GitHub Gist
          const updatedContent = {
            description: 'Visitor count data',
            files: {
              'visitor-count.json': {
                content: JSON.stringify({ visitors: storedVisitors })
              }
            }
          };

          console.log('Updating Gist with new visitor data...');
          await axios.patch(`https://api.github.com/gists/${gistId}`, updatedContent, {
            headers: {
              Authorization: `token ${githubToken}`
            }
          });
          console.log('Gist updated successfully.');
        }

        // Update the visitor count
        setVisits(storedVisitors.length);
      } catch (err) {
        console.error('Error fetching visitor data:', err);
        setError('Failed to fetch visitor data');
      }
    }, 60000); // Throttling the function to run at most once every minute

    fetchVisitorData();
  }, []);

  return (
    <footer>
      {error && <p>Error: {error}</p>}
      <p>Visitor Count: {visits}</p>
    </footer>
  );
};

export default VisitorCount;
